.page {
  font-family: "Poppins", sans-serif;
  color: rgba(255, 255, 255, 1);

  &__body {
    margin: 0;
    min-width: 320px;
    scroll-behavior: smooth;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}
